  list-style: none;
  width: 50%;
  min-width: 50%;
  max-width: 50%;
  padding: 0px 10px;
}
.main1 ul{
  margin: 0px -10px;
}
.main1 li h4{
  border-bottom: 1px solid white;
  height: 100%;
}
.main1 li:last-of-type{
  /* border: none; */
}
.main1 li a{
  padding: 14px 0;
  color: white;
  font-size: 16px;
  display: inline-block;
}
.indexbanner {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.indexbanner .swiper-container,
.indexbanner .swiper-wrapper,
.indexbanner .swiper-slide,
.indexbanner .index_banimg{
  height: 100%;
}
.index_banimg img{
  width: 100%;
  height: 100%;
}
.main1_title{
  padding-left: 30px;
}
.main1_title span{
  display: inline-block;
  width: 200px;
  text-align: center;
  background-color: rgba(0, 0, 0,0.7);
  color: white;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  height: 50px;
  line-height: 50px;
}
.artical_detail{
  position: relative;
  z-index: 10;
  background-color: rgba(50, 50, 50,0.8);
  padding-bottom: 50px;
}
@media(max-width: 768px){
  .main1{
    /* padding: 16px; */
  }
  .main1 li{
    width: 100%;
    min-width: 100%;
    max-width: 100%;
  }
  .main1 li a{
    font-size: 12px;
    display: block;
  }
  .main1_title{
    padding: 0;
    text-align: center;
  }
  .main1_box{
    padding: 16px;
  }
  .artical_detail{
    /* padding: 0px 16px; */
  }
  .artical_detail h1{
    font-size: 28px !important;
    font-weight: bold;
    margin-top: 0 !important;
  }
  .artical_detail h2{
    font-size: 24px !important;
    font-weight: bold;
  }
}